import React, { useEffect, useState } from "react";
import { push } from "connected-react-router";
import {
  Card,
  Button,
  CardMedia,
  Typography,
  IconButton,
  CardContent,
  CardActions,
  CardActionArea,
  CircularProgress,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { store } from "../../../_store/store.config";
import { ILesson } from "../../../_types/lesson.types";
import { thumbnailsFinder } from "../../../_helpers/constants";
import { Favorite, FavoriteBorder } from "@material-ui/icons";
import { addFavorites } from "../../../_actions/profile.actions";
import { DefaultProfileState } from "../../../_types/profile.types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 300,
      maxHeight: 360,
      position: "relative",
      margin: theme.spacing(2),
      [theme.breakpoints.down(350)]: {
        margin: theme.spacing(1),
      },
    },
    cardAction: {
      justifyContent: "space-between",
      margin: "inherit",
    },
    content: {
      minHeight: 150,
    },
    rightChip: {
      backgroundColor: "#fafafac2",
      position: "absolute",
      zIndex: 30,
      right: theme.spacing(1),
      top: theme.spacing(1),
      "&:hover": {
        backgroundColor: "#fafafac2",
      },
    },
    svgSize: {
      color: theme.palette.secondary.main,
      width: 34,
      height: 34,
      "&:hover": {
        color: theme.palette.primary.main,
      },
    },
    loading: {
      maxWidth: 34,
      maxHeight: 34,
      color: theme.palette.secondary.main,
    },
  })
);

type Props = {
  lesson: ILesson;
  user: DefaultProfileState;
  type: string;
};

export const LessonCard: React.FC<Props> = ({ lesson, user, type }) => {
  const classes = useStyles();
  const { title, featured_img, excerpt, author, id } = lesson;
  const { loading, error, favorite } = user;
  const [changed, setChanged] = useState(false);

  const clickHandler = () => {
    store.dispatch(push(`/${type}/${id}`));
  };
  const handleFavorite = () => {
    store.dispatch(
      addFavorites({ title, featured_img, excerpt, author, id, type })
    );
    setChanged(true);
  };

  useEffect(() => {
    if (changed) setChanged(loading);
    else if (error === "Please login!") setChanged(false);
    return () => {
      setChanged(false);
    };
  }, [loading, error, changed]);

  return (
    <Card className={classes.root}>
      <IconButton
        aria-label="add to favorites"
        className={classes.rightChip}
        onClick={handleFavorite}
      >
        {changed ? (
          <CircularProgress className={classes.loading} />
        ) : favorite?.some((fav) => fav.id === id) ? (
          <Favorite className={classes.svgSize} />
        ) : (
          <FavoriteBorder className={classes.svgSize} />
        )}
      </IconButton>
      <CardActionArea onClick={clickHandler}>
        <CardMedia
          component="img"
          alt={title}
          height="140"
          image={thumbnailsFinder(featured_img)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {excerpt}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
           {author}
        </Typography>
        <Button size="medium" color="secondary" onClick={clickHandler}>
           
        </Button>
      </CardActions>
    </Card>
  );
};
